<!-- 手机Loading -->
<template>
  <div class="loading-wrapper-phone">
    <div class="toast toast-loading">
      <!-- <img class="closeModal" @click="closeModal" :src="close_icon" alt="关闭按钮"> -->
      <img  class="market" :src="img_src" alt="商城暂未开通">
      <div class="modalText">
        <span>{couseInfo.title}</span><br>
        <span >{couseInfo.totalDuration}<span>章</span>{couseInfo.totalLecture}<span>节</span></span>
      </div>
      <div class="price-content">
        <div>助学价</div>
        <div ><span class="now-price">{couseInfo.price}</span>元</div>
        <div >原价:<del>¥{couseInfo.originalPrice}</del></div>
      </div>
      <button class="sureBtn" @click="closeModal">确定</button>
    </div>
  </div>
</template>
<script>
  export default {
    props: [
      // 'couseInfo'
    ],
    data() {
      return {
        img_src: require('../assets/img/jerxuat3.png'),
        close_icon: require('../assets/img/guanbi.png')
      }
    },
    methods: {
      closeModal(){
        this.$emit('closeSignInModal',0);
      }
    }
  }
</script>
<style lang="less" scoped>
  .now-price{
    font-size: 40px;
  }
  .loading-wrapper-phone {
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, .5);
    .toast {
      left:50%;
      top: 50%;
      transition-duration: .2s;
      z-index: 100;
      position: fixed;
      border-radius: 5px;
      color: #fff;
      transform: translate(-50%,-50%);
      &>.closeModal{
        position: absolute;
        top: 130px;
        right: 0;
        width: 70px;
      }
      &>.market{
        width: 500px;
      }
      &>.modalText{
        position: absolute;
        top: 35%;
        left: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
        font-size: 30px;
        &>span{
          color: #000;
        }
      }
      &>.price-content{
        text-align: center;
        position: absolute;
        top: 60%;
        left: 50%;
        transform: translate(-50%,-50%);
        &>div:nth-child(1){
          font-size: 40px;
        }
      }
    }
  }
  .sureBtn{
    background-color: #44b336;
    width: 300px;
    height: 70px;
    border-radius: 1.875rem;
    border: none;
    border: 0;
    outline: none;
    color: #fff;
    font-size: 35px;
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);
  }
</style>


