
<style lang="less" scoped>
  .player-container{
    width: 100%;
  }
  body{
    height: auto !important;
  }
  #player {
    width: 100%;
    height: 400px !important;
    position: fixed;
    top: 0;
    background-color: #f3e309;
    max-width: 1040px;
    z-index: 9;
    div {
      height: 400px !important;
    }
  }
  .swiper-container{
    padding-top: 600px;
    margin-bottom: 0;
  }
  #playerRecord {
    position: fixed;
    top: 0;
    left: 50%;
    width: 100%;
    max-width: 1040px;
    transform: translateX(-50%);
    height: 400px !important;
    background-color: #f3e309;
    z-index: 9;
    background-image: url('../../assets/img/default_blue.png');
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover;
    &>div {
      height: 400px !important;
      background-image: url('../../assets/img/nolive_blue.png');
      background-size: 50% 70%;
      background-repeat: no-repeat;
      background-position: center center;
    }
  }
  .middle>div>img {
    vertical-align: middle;
  }
  .middle>div>span {
    vertical-align: middle;
    font-size: 25px;
  }
  .middle>div>a {
    vertical-align: middle;
  }
  .chat_detail {
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    background-color: #fff;
    min-height: 600px;
    padding-bottom: 100px;
  }
  
  .emoji {
    background-image: url('../../../static/images/smile.png');
    background-size: contain;
    background-repeat: no-repeat;
    width: 90px;
    height: 90px;
    vertical-align: middle;
    border: none !important;
    outline: none !important;
    background-color: transparent;
  }
  .middle {
    display: -webkit-flex;
    display: flex;
    height: 120px;
    position: fixed;
    top: 400px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 9;
    background-color: #fff;
    width: 100%;
    background-color: #fff;
    line-height: 120px;
    max-width: 1040px;
  }
  .middle>div {
    height: 120px;
  }
  .middle>div:nth-child(1) {
    -webkit-box-flex: 5;
    flex: 5;
    padding-left: 30px;
  }
  .middle>div:nth-child(1)>img {
    width: 20%;
  }
  .middle>div:nth-child(1)>span {
    display: inline-block;
  }
  .middle>div:nth-child(2) {
    -webkit-box-flex: 5;
    flex: 5;
    text-align: right;
    padding-right: 30px;
  }
  .middle>div:nth-child(2)>img {
    width: 15%;
  }
  .middle>div:nth-child(2)>span {
    font-size: .875rem;
  }
  /* 底部互动区域 */
  .tab_interactive {
    position: fixed;
    top: 500px;
    left: 0;
    display: -webkit-flex;
    display: flex;
    z-index: 9;
    background-color: #fff;
    width: 100%;
    max-width: 1040px;
    left: 50%;
    transform: translateX(-50%);
  }
  .tab_interactive>div {
    flex: 1;
    text-align: center;
    height: 90px;
    line-height: 90px;
    position: relative;
    font-size: 30px;
  }
  .liveSwiperStatus::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 100px;
    height: 6px;
    z-index: 9;
    background-color: #000;
    transform: translateX(-50%);
  }
  .chat_with {
    color: #fff;
    background-color: #44b336;
    border: 0;
    border-radius: 20px 20px 20px 0;
    font-size: 25px;
    padding: 10px;
  }
  /* 聊天 START */
  .course_detail {
    background-color: #fff;
  }
  .chat_item_user {
    width: 100%;
    clear: both;
    margin: 20px 0;
    height: 100%;
    background-color: #fff;
    min-height: 130px;
    &>div {
      float: right;
      display: inline-block;
    }
    &>div:nth-child(1) {
      text-align: center;
      &>img {
        width: 90px;
        height: 90px;
        border-radius: 50%;
        line-height: 90px;
      }
    }
    &>div:nth-child(2) {
      position: relative;
      text-align: right;
      margin: 0 30px;
      font-size: 25px;
      &>.chat_name {
        height: 90px;
        line-height: 90px;
        font-size: 25px;
      }
      &>p {
        margin: 0;
        background-color: #e9e9e9;
        padding: 20px 20px;
        border-radius: 20px;
        text-align: right;
        max-width: 350px;
        word-wrap: break-word;
        word-break: normal;
        font-size: 30px;
        &>img {
          width: 50px;
          height: 50px;
        }
      }
      &>small {
        position: absolute;
        z-index: 2;
        right: 400px;
        top: 50%;
        transform: translateY(-50%);
        width: 150px;
        font-size: 25px;
      }
    }
  }
  .chat_item {
    width: 100%;
    clear: both;
    padding-top: 20px;
    &>div {
      float: left;
      display: inline-block;
    }
    &>div:nth-child(1) {
      text-align: center;
      &>img {
        width: 90px;
        height: 90px;
        border-radius: 50%;
        line-height: 90px;
      }
    }
    &>div:nth-child(2) {
      margin: 0 20px;
      position: relative;
      &>.chat_name {
        height: 50px;
        line-height: 50px;
        font-size: 25px;
      }
      &>p {
        margin: 0;
        background-color: #e9e9e9;
        width: auto;
        height: auto;
        padding: 20px 30px;
        border-radius: 20px;
        word-wrap: break-word;
        word-break: normal;
        max-width: 400px;
        &>img {
          width: 50px;
          height: 50px;
        }
      }
      &>small {
        position: absolute;
        top: 50%;
        right: -130px;
        transform: translateY(-50%);
        font-size: 25px;
      }
    }
  }
  /* 聊天输入框 START */
  .chat_hot {
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: #fff;
    height:70px;
    width: 100%;
    max-width: 1040px;
    text-align: center;
    z-index: 9;
    padding-top: 20px;
    left: 50%;
    transform: translateX(-50%);
  }
  .chat_hot>div>input {
    height: 50px;
    text-indent: 1em;
    outline: none;
    border: 3px solid #aaa;
    border-radius: 20px;
    width: 360px;
    margin: 0 30px;
  }
  .chat_hot>div>span {
    color: #000;
    &>img{
      width: 50px;
      height: auto;
      vertical-align: middle;
      margin: 0 10px;
    }
  }
  // 用户列表头像
  .user-list {
    display: flex;
    width: 100%;
    background-color: #fff;
    &>div:nth-child(1) {
      flex: 2;
      text-align: center;
      &>img {
        width: 100px;
        height: 100px;
        vertical-align: middle;
      }
    }
    &>div:nth-child(2) {
      flex: 8;
      vertical-align: middle;
      line-height: 120px;
    }
  }
  .user-margin{
    min-height: 600px;
  }
  .questionnaire{
    position: fixed;
    top: 60%;
    right: 0;
    background-image: url('../../assets/img/dc2.png');
    z-index: 1000;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100% 100%;
    background-color: rgba(255, 255, 255, 0.6);
    overflow: hidden;
    height: 50px;
    transform: translateX(0);
    transition: translateX .3 ease-in;
    &>span{
      display: inline-block;
      height: 50px;
      vertical-align: middle;
    }
    &>span:nth-child(1){
      width: 60px;
    }
    &>span:nth-child(2){
      width: 150px;
      color: #fff;
      height: 40px;
    }
  }
  .transformCss{
    transform: translateX(160px)
  }
  .pool_effect{
    font-size: 25px;
  }
</style>
<style>
.pool>p>img{
  width: 50px;
}
@media screen and (min-width: 1000px){
  .polyvFlashObject{
    height: 400px;
  }
}

</style>

<template>
  <div class="player-container">
    <!-- 直播页面 视频直播区域 START -->
    <div id='player' class="live_pannel"></div>
    <!-- <div id='playerRecord' class="live_pannel" v-if="playerRecord">
      <div></div>
    </div> -->
    <!-- 直播页面 视频直播区域 END -->
    <!-- 中部的签到区域 START -->
    <section class="middle">
      <div>
        <img :src="teacherAvatar" alt="讲师头像">
        <span>{{teacherName}}</span>
      </div>
      <div>
        <!-- <div @click="intergal"> -->
        <!-- <img src="./static/images/qiandao.png" alt="签到">
          <a>打开签到</a> -->
      </div>
    </section>
    <!-- 签到区域 END -->
    <!-- 直播互动区域 START -->
    <section class="tab_interactive">
      <div @click="liveInteraction" :class="{'liveSwiperStatus': !SWIPER_CODE_INDEX}">直播互动</div>
      <div @click="memberInteraction" :class="{'liveSwiperStatus': SWIPER_CODE_INDEX}">
        <span>在线同学</span>
        <span class="chat_with">{{chatUserList.length}}</span>
      </div>
    </section>

    <div class="swiper-container" id="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <section class="chat_detail">
            <div class="course_detail" id="chat" v-for="(item, index) in chatList" :key="index">
              <div class="chat_item_user" v-if="item.selfState === 'Y'">
                <div>
                  <img class="chat_ava_user" :src="item.user.pic &&　item.user.pic.indexOf('http') === -1 ? productURL + item.user.pic : item.user.pic" alt="聊天头像">
                </div>
                <div class="pool">
                  <div>{{item.user.nick}}</div>
                  <p v-html="convertEmotions(item.content)"></p>
                  <small class="pool_time">{{item.time | dateDiff}}</small>
                </div>
              </div>
              <div class="chat_item" v-if="item.selfState === 'N'">
                <div>
                  <img class="chat_ava" :src="item.user.pic &&　item.user.pic.indexOf('http') === -1 ? productURL + item.user.pic : item.user.pic" alt="聊天头像">
                </div>
                <div class="pool">
                  <div class="chat_name">{{item.user.nick}}</div>
                  <p class="pool_effect" v-html="convertEmotions(item.content)"></p>
                  <small class="pool_time">{{item.time | dateDiff}}</small>
                </div>
              </div>
            </div>
          </section>
        </div>
        <div class="swiper-slide">
          <div class="user-margin">
            <div class="user-list" v-for="(userItem, index) in chatUserList" :key="index">
              <div><img :src="userItem.pic" alt="用户头像"></div>
              <div>{{userItem.nick}}</div>
            </div>
          </div>
        </div>
        
      </div>
    </div>

     <!-- 签到积分 START -->
    <transition-group enter-active-class="zoomIn" leave-active-class="zoomOut">
      <timeout key="timeout" v-if="showTimeOut" @closeSignInModal="closeSignInModal" :title="title" class="animated"></timeout>
    </transition-group>
    <!-- 签到积分 END -->
    <!-- 直播互动区域 END -->
    <div class="chat_hot" ref="chat_hot">
      <div>
        <emoji v-if="showEmojiModal" @getEmoji="getEmoji"></emoji>
        <input type="text" v-model="chatContent" @focus="chatContentFoucus" placeholder="说两句......." ref="chatContentObj">
        <span @click="emojiEvent"><img :src="changeEmoji" alt="表情"></span>
        <span class="send" @click="sendChatMsg"><img src="../../../static/images/turn.png" alt="发送"></span>
      </div>
    </div>
    <div :class="{questionnaire: true}" :style="{transform: transformCss ? 'translateX(0)' : 'translateX(160px)'}">
      <span @click="showQuetEvent"></span><span>调查问卷</span>
    </div>
  </div>
</template>
<script>
  import timeout from '@/components/timeout'
  import emoji from '@/components/emoji'
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('userModule')
  export default {
    name: 'live',
    components: {
      timeout,
      emoji,
    },
    computed: {},
    destroyed() {
      this.isActivation = 0
      this.socket.close()
    },
    data() {
      return {
        // 课程列表
        liveList: [],
        // 保利威视请求地址
        polyvUrl: '/api/polyv/realtime',
        // 在线学生数量
        studentCount: 100,
        // 讲师头像
        teacherAvatar: '',
        // 讲师昵称
        teacherName: '',
        rfliveStatus: 1,
        // 录播视频路径设置
        recordFilesSrc: '',
        // 直播控件设置状态
        player: false,
        // 录播视频控件设置状态
        playerRecord: 0,
        // 聊天列表
        chatList: new Array(),
        // 轮播状态
        liveSwiperStatus: 0,
        // 表情使用
        chatContent: '',
        // 保利威视返回的token
        tokens: '',
        // soket连接
        socket: Object,
        // 3分钟后显示联系课程顾问提示框
        showTimeOut: 0,
        // 标题
        title: '报名课程',
        // 显示emoji表情控件
        showEmojiModal: 0,
        // 表情状态
        emojiCountStatus: false,
        chatUserList: new Array(),
        changeEmoji: require('../../assets/img/smile.png'),
        // 课程信息, 用来查询播放状态, 历史信息, 用户开课状态
        courseInfo: null,
        transformCss: 0,
        swiperObj: null
      }
    },
    mounted() {

      window.onscroll = () => {
        return
      }
      
      setTimeout(() => {
        // 初始化直播播放器
        this.player = polyvObject('#player').livePlayer({
          'width': '100%',
          'height': '200px',
          'uid': 'e77rl86axa',
          'vid': this.$route.params.id
        });
      }, 1000)

      let _this = this
      this.swiperObj = new Swiper("#swiper-container", {
      passiveListeners: false,
      observer: true, //修改swiper自己或子元素时，自动初始化swiper    重要
      observeParents: true, //修改swiper的父元素时，自动初始化swiper  重要
      autoHeight:true,
      on: {
        slideChangeTransitionEnd() {
          if(this.activeIndex){
            document.scrollingElement.scrollTop = 0
            _this.$store.state.userModule.SWIPER_CODE_INDEX = 1
          }else{
            document.scrollingElement.scrollTop = document.scrollingElement.scrollHeight
            _this.$store.state.userModule.SWIPER_CODE_INDEX = 0
          }
        },
      }
    })
      
      
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      // 获取频道信息
      this.axios.get('/api/getChannelsStatus', {
        params: {
          vid: this.$route.params.id
        }
      }).then(data => {
        if (data.data.indexOf('live') !== -1) {
          this.player = true;
          // 得到课程信息
          this.axios.get('/api/course', {
            params: {
              courseId: this.$route.params.courseId
            }
          }).then(data => {

            this.courseInfo = data.data

            var DatefinishTime = new Date();
            var finishTime = DatefinishTime.getFullYear() + "-" + (DatefinishTime.getMonth() + 1) + "-" + DatefinishTime.getDate()
            var DatestartTime = new Date(DatefinishTime);
            DatestartTime.setDate(DatefinishTime.getDate() - 0);
            var startTime = DatestartTime.getFullYear() + "-" + (DatestartTime.getMonth() + 1) + "-" + DatestartTime.getDate()
            // 获取历史消息
            this.axios.get('/api/getHistory', {
              params: {
                startDay: startTime,
                finishTime: finishTime,
                vid: this.$route.params.id
              }
            }).then(data => {
              this.chatList = data.data.data.slice(data.data.data.length - 300, data.data.data.length);
              this.chatList.forEach(ele => {
                if (ele.user.userId == this.LOGIN_USER_OBJECT.user.userId) {
                  ele.selfState = 'Y'
                } else {
                  ele.selfState = 'N'
                }
              });
              this.toBottom(0)
              this.$Indicator.close()

            }).catch(err => {
              console.log(err);
            });
          }).catch(err => {
            console.log(err);
          });
        } else {
          this.axios.get('/api/recordFiles', {
            params: {
              vid: this.$route.params.id
            }
          }).then(data => {
            this.playerRecord = 1;
            var recFilesArr = data.data.data;
            if (recFilesArr.length > 0) {
              this.rfliveStatus = 0;
            }
            this.recordFilesSrc = recFilesArr[recFilesArr.length - 1].url;

            var DatefinishTime = new Date();
            var finishTime = DatefinishTime.getFullYear() + "-" + (DatefinishTime.getMonth() + 1) + "-" + DatefinishTime.getDate()
            var DatestartTime = new Date(DatefinishTime);
            DatestartTime.setDate(DatefinishTime.getDate() - 0);
            var startTime = DatestartTime.getFullYear() + "-" + (DatestartTime.getMonth() + 1) + "-" + DatestartTime.getDate()
            this.axios.get('/api/getHistory', {
              params: {
                startDay: startTime,
                finishTime: finishTime,
                vid: this.$route.params.id
              }
            }).then(data => {
              this.chatList = data.data.data.slice(data.data.data.length - 300, data.data.data.length);
              this.chatList.forEach(ele => {
                if (ele.user.userId == this.LOGIN_USER_OBJECT.user.userId) {
                  ele.selfState = 'Y'
                } else {
                  ele.selfState = 'N'
                }
              });
              this.toBottom(0)
              this.$Indicator.close()
            }).catch(err => {
              console.log(err)
            });

          }).catch(err => {
            console.log(err)
          });
        }
      }).catch(err => {
        console.log(err)
      });
      // 获取课程信息
      this.axios.get('/api/live', {
        params: {
          courseId: this.$route.params.courseId,
        }
      }).then(data => {
        this.teacherName = data.data.result[0].teacherName;
        this.teacherAvatar = data.data.result[0].teacherAvatar.indexOf('http') == -1 ? this.productURL + data.data.result[0].teacherAvatar : data.data.result[0].teacherAvatar;
      }).catch(err => {
        console.log(err)
      })

      this.axios.get('/api/watchtoken/gettoken').then(data => {
        this.tokens = data.data;
        this.getSockets(data.data)
      }).catch(err => {
        console.log(err)
      });
    },
    computed: mapState([
      'LOGIN_USER_OBJECT',
      'SWIPER_CODE_INDEX'
    ]),
    watch: {
      LOGIN_USER_OBJECT(state){},
      SWIPER_CODE_INDEX(state){}
    },
    methods: {
      liveInteraction(){
        this.swiperObj.slideTo(0, 500, false) // 切换到第一个slide，速度为1秒
        this.$store.state.userModule.SWIPER_CODE_INDEX = 0
      },
      memberInteraction(){
        this.swiperObj.slideTo(1, 500, false) // 切换到第二个slide，速度为1秒
        this.$store.state.userModule.SWIPER_CODE_INDEX = 1
      },
      showQuetEvent(){
        if(this.transformCss){
          this.transformCss = 0
        }else{
          this.transformCss = 1
        }
      },
      // 发送信息
      sendChatMsg() {
        // 未输入评论内容
        if (!this.chatContent || this.chatContent.length === 0 || this.chatContent.charAt(0) === '') {
          this.$Toast({
            message: '说点爱听的吧',
            position: 'middle',
            duration: 1000
          })
          // input光标聚焦
          this.$refs.chatContentObj.focus()
          return
        }
        this.socket.emit('message', JSON.stringify({
          'EVENT': 'SPEAK',
          'values': [this.chatContent],
          'roomId': this.$route.params.id
        }));
        var item = {
          user: {
            channelId: this.$route.params.id,
            nick: this.LOGIN_USER_OBJECT.user.nickname,
            pic: this.LOGIN_USER_OBJECT.user.avatar,
            userId: this.LOGIN_USER_OBJECT.user.userId
          },
          time: new Date().getTime(),
          selfState: 'Y',
          content: this.chatContent
        }
        this.chatList.push(item)
        this.toBottom(100);
        this.chatContent = ''
      },
      toBottom(time) {
        setTimeout(() => {
          document.scrollingElement.scrollTop = document.scrollingElement.scrollHeight
        }, time)
      },
      // 文本内容追加表情
      getEmoji(data) {
        this.chatContent = `${this.chatContent}${data}`
        this.chatContentFoucus()
      },
      // 聚焦/隐藏表情
      chatContentFoucus() {
        // this.changeEmoji = require('../../assets/img/smile.png')
        // this.showEmojiModal = 0
        this.emojiCountStatus = true
        let resizeHeight = document.documentElement.clientHeight
      },
      // 父子元素表情信息传递
      emojiEvent() {
        this.changeEmoji = require('../../assets/img/green.png')
        if (!this.emojiCountStatus) {
          this.showEmojiModal = 1
          this.emojiCountStatus = !this.emojiCountStatu
          this.$refs.chatContentObj.blur()
        } else {
          this.changeEmoji = require('../../assets/img/smile.png')
          this.showEmojiModal = 0
          this.emojiCountStatus = !this.emojiCountStatus
        }
      },
      closeSignInModal(data) {
        this.showTimeOut = data;
      },
      getSockets(token2) { //sockets的一个连接
        var chatHost = 'http://chat.polyv.net:80';
        var chatToken = token2;
        //连接socket
        //ps:判断浏览器支持WebSocket则优先使用
        var supportsWebSockets = 'WebSocket' in window || 'MozWebSocket' in window;
        if (supportsWebSockets) {
          this.socket = io.connect(chatHost, {
            'query': 'token=' + chatToken,
            'transports': ['websocket']
          });
        } else {
          this.socket = io.connect(chatHost, {
            'query': 'token=' + chatToken,
            'transports': ['polling']
          });
        }
        this.socket.on('connect', () => {

          
          // 获取在线用户列表
          this.axios.get('/api/onlineUserList', {
            params: {
              roomId: this.$route.params.id
            }
          }).then(data => {
            if (data.data.userlist) {
              this.chatUserList = data.data.userlist
            }
          }).catch(err => {
            console.log(err)
          });

          //连接服务器成功
          var user = this.LOGIN_USER_OBJECT.user
          //连接服务器成功
          let userInfo = { //用户登录
            'EVENT': 'LOGIN',
            'values': [user.nickname, user.avatar.indexOf('http') !== -1 ? user.avatar : `${this.productURL}/${user.avatar}`, user.userId], //昵称、头像地址、用户id
            'roomId': this.$route.params.id
          }
          if(user.isVip === 'Y'){
            userInfo.values[0] = `VIP_${user.nickname}`
          }else if(user.className){
            userInfo.values[0] = `${user.className}_${user.nickname}`
          }
          this.socket.emit('message', JSON.stringify(userInfo));

        });
        this.socket.on('message', (message) => { //接收信息事件
          var data = JSON.parse(message)
          // console.log(data.EVENT);
          this.studentCount = data.onlineUserNumber
          if (data && data.EVENT) { //根据返回的不同事件类型作相应处理
            switch (data.EVENT) {
              case 'CLOSEROOM': // room closed
                break;
              case 'GONGGAO': //系统公告
                break;
              case 'SPEAK': // 用户发言
                var item = {
                  user: data.user,
                  time: data.time,
                  selfState: 'N',
                  content: data.values[0]
                }
                this.chatList.push(item)
                if(this.$router.history.current.path.startsWith('/live')){
                  this.toBottom(0)
                }
                break;
              case 'REWARD': //奖励信息
                break;
              case 'QUESTION':
                break;
              case 'CLOSE_QUESTION':
                break;
              case 'ANSWER':
                break;
              case 'ERROR': // 出错了
                break;
              case 'KICK': // 用户被踢
                alert("您被提出了频道。");
                break;
              case 'REMOVE_HISTORY': //清空聊天记录
                break;
              case 'CLOSE_DANMU': //关闭弹幕
                break;
              default:
                break;
            }
          }
        });
      }
    }
  }
</script>



